.DadosUser{   
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}
.DadosUser .card--conteiner{
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 0 10px 15px 0 rgba(0, 0, 0 , .4);
}
.card--conteiner h1{
    margin: 10px auto;
}
.Dados .title{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    
}
.title--item{
    padding: 3px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    color:rgb(26, 131, 57)
}
.title--item h4{
    padding: 0px 15px;
}
.Dados{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 10px;
}
.Dados .card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 5px ;
    margin: 10px;
}
.Dados .item{  
    padding: 4px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    background-color: rgba(0, 0, 0, 0.01);
    width: 100%;
}
.Dados .item--input{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
}
.Dados h4{
    padding: 0px 5px;

}
.Dados h5{
    padding: 0px 5px;
    margin: 0px;
}
.Dados input , .Dados select{
    padding: 2px 4px;
    margin: 0px;
    text-align: center;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0 , .1);
    border: 0;
    outline: 0;
    width: 85px;
    background-color: white;
}
.DadosUser button {
    padding: 4px;
    margin: 10px 0px 0px 0px;
    text-align: center;
    cursor: pointer;
    outline: 0;
    border: 0;
    width: 100%;
    font-size: 18pt;
    background-color: rgba(35, 162, 60, 0.727);
    color: rgb(255, 255, 255);
    transition: .5s;
}
.DadosUser button:hover {
    transform: scale(1.05);
    transition: .5s;
}