.Protifolio{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card--conteriner{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70%;
    border-radius: 2px;
    padding: 5px;
}
.Protifolio .card iframe,
.Protifolio .card img
{    
    height: 85vh;
    width: calc(100% - 4px);
    padding:1px;
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
}
.Protifolio .card--seta--left, .Protifolio .card--seta--right{
    position:absolute;
    width:40px;
    background-color: rgba(255,255, 255, .6);
    filter:invert(1);
    border-radius: 10%;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;    
    padding: 0px 5px;
    overflow:hidden;
    opacity: 1;
    transition: 0.2s;
    transform: scale(.9);
    cursor: pointer;
}
.Protifolio .card--seta--left{
    left:15.5%;
}
.Protifolio .card--seta--right{    
    right:15.5%;
}

.Protifolio .card--seta--left:hover, .Protifolio .card--seta--right:hover{    
    transition: 0.2s;
    transform: scale(1);
    background-color: rgba(255,255, 255, 1);
}
@media (max-width: 700px) {
    .Protifolio .card iframe,
    .Protifolio .card img,
    .Protifolio .card iframe 
    {
        height: 60vh;
    }  
    .Protifolio .card
    {       
        width: 100%;
    }  
    
    .Protifolio .card--seta--left{
        left:15px;
    }
    .Protifolio .card--seta--right{    
        right:15px;
    }    
}