.Capa{
    width: 100%;
    height:100vh;
    padding: 0px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
video{
    width: inherit;
    height: inherit;
    object-fit: cover;
    z-index: -1;    
}
.Capa--info{
    z-index: 5; 
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Capa--info h3{
    width: 100%;
    z-index: 5;
    color: #fff;
    font-weight: bold;
    margin-top: -200px
}
.Capa--info .Capa--seta--down{
    -webkit-animation: MoveUpDown .5s linear  1s infinite alternate;
            animation: MoveUpDown .5s linear  1s infinite alternate;
    font-size:80px;
    -webkit-filter:invert(1);
            filter:invert(1);
    margin-top: 0px
}
@-webkit-keyframes MoveUpDown {
    from {
        font-size: 50px;
    }
    to { 
        font-size: 60px;
    }
}
@keyframes MoveUpDown {
    from {
        font-size: 50px;
    }
    to { 
        font-size: 60px;
    }
}
.ComoFunciona{
    width: 100%; 
    min-height: 60vh ;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    margin: 10px;
}
.ComoFunciona h1{ 
    margin-top: 50px;
}
.ComoFunciona .Card--container{   
    margin-top: 30px; 
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;   
}
.ComoFunciona .Card{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;  
    margin: 5px auto ;
    cursor: pointer;
    width: 250px;
}
.ComoFunciona .Card h3{
    font-size:14pt;
}
.ComoFunciona .Card h4{
    font-size:smaller;
    color:rgb(26, 131, 57)
}
.ComoFunciona .Card img{
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 2px;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    transition: .5s;
    border-radius: 2px solid;
}

