.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 9;
}
.Header--logo{
    height: 40px;
    margin: 5px;
    cursor: pointer;
    display:inline-flex;
    justify-content: center;
    align-items: center;
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--logo h4{
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-left: 5px;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center; 
}
.Header--menu button{
    background-color: #14141400;
    margin: 0px 5px;
    padding: 0px 5px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.Header--contact{
    height: 25px;
    margin: 0px 5px;
    display: inline-flex;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;    
}
.Header--contact img{
    width: auto;
    height: 100%;
}
@media (max-width: 600px){ 
    .Header--logo h4{
        opacity: 0;
    }
}
.Footer{
    height: 100%;
    width: 100%;
    padding: 20px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: rgba(128, 128, 128, 0.193);
}
.Protifolio{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card--conteriner{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70%;
    border-radius: 2px;
    padding: 5px;
}
.Protifolio .card iframe,
.Protifolio .card img
{    
    height: 85vh;
    width: calc(100% - 4px);
    padding:1px;
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
}
.Protifolio .card--seta--left, .Protifolio .card--seta--right{
    position:absolute;
    width:40px;
    background-color: rgba(255,255, 255, .6);
    -webkit-filter:invert(1);
            filter:invert(1);
    border-radius: 10%;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;    
    padding: 0px 5px;
    overflow:hidden;
    opacity: 1;
    transition: 0.2s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    cursor: pointer;
}
.Protifolio .card--seta--left{
    left:15.5%;
}
.Protifolio .card--seta--right{    
    right:15.5%;
}

.Protifolio .card--seta--left:hover, .Protifolio .card--seta--right:hover{    
    transition: 0.2s;
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255,255, 255, 1);
}
@media (max-width: 700px) {
    .Protifolio .card iframe,
    .Protifolio .card img,
    .Protifolio .card iframe 
    {
        height: 60vh;
    }  
    .Protifolio .card
    {       
        width: 100%;
    }  
    
    .Protifolio .card--seta--left{
        left:15px;
    }
    .Protifolio .card--seta--right{    
        right:15px;
    }    
}
