    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;    
}

.Preco{ 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    
}
.Preco .card--preco{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5px ;
    border-radius: 10px;
    margin: 5px;
}
.Preco .card--conteiner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    
}
.Preco .card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 335px;
    min-height: 310px; 
